
	.music-lrc {
			position: absolute;
			width: 100%;
			/*height: 600px!important;*/
			top: 85px;
			left:0;
			bottom: 75px;
			opacity: 1;
			overflow: hidden;
			transition: opacity .3s;
			z-index: 90;
			/*background: rgba(0, 0, 0, 0.1);*/
			/*box-shadow: 2px 0 2px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(255, 255, 255, 0.2);*/
		}

	.lrc-ul {
		width: 630px;
		height: auto;
		position: absolute;
		left: 50%;
		margin-left: -315px;
		text-align: center;
		transform: translateY(0);
		transition: all .5s;
	}

	#canvas1 {
		width: 100%!important;
		height: 620px!important;
		position: absolute;
		top: -160px;
		left: 0;
		z-index: 999;
		/*background: rgba(0, 0, 0, 0.5);*/
	}

	.lrc-ul li {
		padding: 5px 0px;
		font-size: 19px;
		height: auto;
		box-sizing: border-box;
		cursor: pointer;
		transition: all .6s linear;
		font-weight: bold;
		font-family: kaiTi;
		text-shadow: 1px -1px 2px rgba(0,0,0,0.6);
		height: 40px;
		line-height: 40px;
		color: rgba(255,255,255,0.8);
	}

	.lrc-ul .playing {
		color: rgb(255, 135, 156);
		font-size: 19px;
		text-shadow: 1px -1px 2px rgba(0,0,0,0.6);
		transition: all .8s linear;
	}



