
body {
    height: 100%;
    width: 100%;
    background-image: url('http://p46wrelzu.bkt.clouddn.com/2018m1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: hidden;
    overflow-x: hidden;
    -webkit-transition: background-image 1.1s;
    transition: background-image 1.1s;
}

